import React, {useState, useEffect} from 'react';
import PropTypes from 'prop-types';

import {
	Panel,
	PanelHeader,
	PanelHeaderBack,
	Group,
	SimpleCell
}	from '@vkontakte/vkui';

import {
	Icon28NameTagOutline,
	Icon28MessagesOutline,
	Icon28FavoriteOutline,
	Icon28WarningTriangleOutline,
	Icon28EmployeeOutline
}	from '@vkontakte/icons';

const Admin = props => {
	const [datas, setDatas] = useState("Данные не получены");
	useEffect(() => {
		fetch("https://bots.boilama.xyz/ntp/stat/user.php?key=pJR295F0PqIHDTBYHgs6BBfGG&user=266890472")
		.then(data => data.json())
		.then(data => {
		  setDatas(data.ball)
		  console.log(data)
		})
		 }, [ ] );
	return (
	<Panel id={props.id}>
		<PanelHeader left={<PanelHeaderBack onClick={props.go} data-to="Debug"/>}>Карточка [DEBUG]</PanelHeader>
		<Group>
            <SimpleCell before={<Icon28NameTagOutline />} description="Ник">{datas}</SimpleCell>
			<SimpleCell before={<Icon28MessagesOutline />} description="Символы">{datas}</SimpleCell>
			<SimpleCell before={<Icon28FavoriteOutline />} description="Рейтинг">{datas}</SimpleCell>
			<SimpleCell before={<Icon28WarningTriangleOutline />} description="Предупреждений">{datas}</SimpleCell>
			<SimpleCell before={<Icon28EmployeeOutline />} description="Роль">{datas}</SimpleCell>
          </Group>
	</Panel>
	);
};

Admin.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Admin;